@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
:root{
  --primary-color: #263238;
  --card-color: #607D8B;
  --default-top: 2em;
  font-family: 'Open Sans', sans-serif;
}
*{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
main{
  width: 80%;
  margin: auto;
}
footer{
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  min-height: 10vh;
  background-color: var(--primary-color);
}


nav{
  background-color: var(--primary-color);
  min-height: 10vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: white;
}
nav a{
  color: white;
  text-decoration: none;
}
nav a:hover{
  text-decoration: underline;
  text-underline-position: under;
}
nav ul{
  list-style-type: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
nav ul li{
  margin-right: .9em;
}
nav h1{
  cursor: pointer;
}

footer h1{
	cursor: pointer;
}

.turkey-cases{
  padding-top: var(--default-top);
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  -webkit-column-gap: .9em;
     -moz-column-gap: .9em;
          column-gap: .9em;
}
.total-case{
  background-color: var(--card-color);
  min-height: 50vh;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  padding: .9em;
  color: white;
}
.total-case h1{
  font-size: 5em;
}
.total-case p{
  font-size: 2em;
}
.case-infos{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
}
.case-infos .case-card{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  background-color: var(--card-color);
  min-height: 15vh;
  padding: .9em;
  color: white;
}
.case-card h2{
  font-size: 2.5em;
}



.last-news{
	padding-top: var(--default-top);
}
marquee span{
	margin-right: 2em;
}
marquee span a{
	color: black;
	text-decoration: none;
}
marquee span a:hover{
	text-decoration: underline;
	text-underline-position: under;
}

.most-affected-countries{
	padding-top: var(--default-top);
	padding-bottom:  var(--default-top);
	display: grid;
	-webkit-column-gap: .9em;
	   -moz-column-gap: .9em;
	        column-gap: .9em;
	row-gap: .9em;
	grid-template-columns: 1fr 1fr 1fr;
	justify-items: center;
}
.countries-card{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	color: white;
	background-color: var(--card-color);
	width: 100%;
	height: 10vh;
}
.countries-card ul{
	display: none;
	list-style-type: none;
}
.countries-card:hover ul{
	display: block;
}
@media screen and (max-width: 900px){
.most-affected-countries{
		grid-template-columns: 1fr 1fr;
}
}

.charts{
	padding-top: var(--default-top);
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.world-stat{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	text-align: center;
}





.all{
	height: 60vh;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	text-align: center;
}
.recovered h1{
	color: #4CAF50;
}
.deaths h1{
	color: #B71C1C;
}
.total h1{
	font-size: 3em;
}
.total p{
	font-size: 2em;
}

.today{
	padding-top: var(--default-top);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	margin-bottom: var(--default-top);
}
.today-card{
	border: 2px solid #FF6F00;
	padding: 2em;
}
.today h1{
	font-size: 2em;
}
.today p{
	font-size: 1.3em;
}
.total-cases h1{
	color: #FF9800;
}
@media screen and (max-width: 900px){
.today{
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		    -ms-flex-direction: column;
		        flex-direction: column;
}
.today-card{
		margin-top: var(--default-top);
		margin-bottom: var(--default-top);
}
}

.news{
		padding-top: var(--default-top);
		padding-bottom: var(--default-top);
	height: auto;
}


.new-card{
	height: auto;
	border-radius: 20px;
	border: 3px solid black;
	display: grid;
	grid-template-columns: 1fr 1fr;
	margin-bottom: var(--default-top);
}
.new-card button{
	border-radius: 20px;
	padding: .9em;
	border: none;
	background-color: #1A237E;
	color: white;
	width: 250px;
}
.new-body{
	text-align: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}
.new-card{
	padding: .9em;
}
.new-card img{
